/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 2rem;
  line-height: 1.5;
}

/* Scroll */
body::-webkit-scrollbar {
  width: 12px;
  /* ancho de la barra de desplazamiento */
}

body::-webkit-scrollbar-track {
  background: transparent;
  /* color del área de desplazamiento */
}

body::-webkit-scrollbar-thumb {
  background-color: rgba(233, 148, 1, 0.9);
  /* color del botón de desplazamiento */
  border-radius: 2rem;
  /* redondez del botón de desplazamiento */
  /* border: 3px solid orange;  relleno alrededor del botón de desplazamiento */
}

[class$=__container] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

a {
  text-decoration: none;
}

.header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(../img/header.webp);
  background-size: cover;
  background-position: center;
  padding: 5rem 0;
  position: relative;
}
.header__bar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__bar {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
}
.header__guitar {
  display: none;
}
@media (min-width: 1024px) {
  .header__guitar {
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
    animation: showImage 1.5s ease-in-out;
  }
}

@keyframes showImage {
  0% {
    opacity: 0;
    transform: translateX(-5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.navigation {
  display: flex;
  gap: 2rem;
  margin-top: 5rem;
  position: relative;
  z-index: 1;
}
@media (min-width: 768px) {
  .navigation {
    margin-top: 0;
  }
}
.navigation__link {
  color: #FFFFFF;
  text-transform: uppercase;
  padding: 0.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  transition: color 0.2s ease-in-out;
}
.navigation__link:hover {
  color: #e99401;
}

.model {
  color: #FFFFFF;
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .model {
    text-align: left;
    width: 50%;
  }
}
.model__name {
  font-size: 8rem;
}
.model__price {
  font-size: 6rem;
  color: #e99401;
  font-weight: 900;
  line-height: 0;
}
.model__link {
  margin-top: 2rem;
  padding: 1rem 5rem;
  border: 2px solid #e99401;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1.4rem;
  transition: background-color 0.2s ease-in-out;
}
.model__link:hover {
  background-color: #e99401;
}